body {
  color: #333;
  background-color: #f0f0f0;
  font-family: Arial, sans-serif;
}

h1 {
  text-align: center;
}

#game-container {
  max-width: 600px;
  background-color: #fff;
  border-radius: 10px;
  margin: 0 auto;
  padding: 20px;
  box-shadow: 0 2px 10px rgba(0, 0, 0, .3);
}

#problem {
  text-align: center;
  margin-bottom: 20px;
  font-size: 24px;
}

#answer-form {
  align-items: center;
  display: flex;
}

#answer-input {
  width: 100%;
  margin-right: 10px;
  padding: 5px;
  font-size: 20px;
}

#submit-button {
  color: #fff;
  cursor: pointer;
  background-color: #06c;
  border: none;
  border-radius: 5px;
  padding: 5px 10px;
  font-size: 20px;
}

#submit-button:hover {
  background-color: #049;
}

#score {
  text-align: center;
  margin-top: 20px;
  font-size: 20px;
}

